/* VARIABLES
*/
/* FONT-FAIMLY
*/
.raleway,
.syne,
.karla {
  font-optical-sizing: auto;
  font-weight: 100;
  font-style: normal;
}

.raleway {
  font-family: "Raleway", sans-serif !important;
}

.syne {
  font-family: "Syne", sans-serif !important;
}

.karla {
  font-family: "Karla", sans-serif !important;
}

::-webkit-scrollbar {
  display: none;
}

.customScrollbarContainer {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  width: -moz-max-content;
  width: max-content;
  z-index: 999;
  background-color: #242424;
}
.customScrollbarContainer .customScrollbar {
  width: 4px;
  height: 0px;
  background: #C32865;
}

.preloader__Script {
  opacity: 0 !important;
  visibility: hidden !important;
}

.preloader {
  transition: 0.5s ease;
  width: 100%;
  height: 100vh;
  background-color: #242424;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 900;
  display: grid;
  place-items: center;
}
.preloader p {
  text-transform: capitalize;
  font-size: 18px;
  font-weight: 300;
  color: white;
}

.startup {
  visibility: visible;
  width: 100%;
  height: 100vh;
  background-color: #242424;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1000;
  display: grid;
  place-items: center;
  animation: startup 0.5s ease forwards;
  animation-delay: 4s;
}
.startup .content {
  width: -moz-max-content;
  width: max-content;
  height: -moz-max-content;
  height: max-content;
  position: relative;
}
.startup .content .spanWrap {
  display: none;
  position: absolute;
  top: 0;
  left: 0;
  transform: translateX(-50%);
  display: flex;
  align-items: center;
  color: white;
  transition: ease 0.4s 1.5s;
  animation: spanWrap 0.5s ease forwards;
  animation-delay: 2s;
}
.startup .content .spanWrap .span {
  text-transform: capitalize;
  font-size: 25px;
  font-weight: 300;
  padding: 0 7px;
  transform: translateY(50px);
  opacity: 0;
}
.startup .content .spanWrap .span1 {
  animation: span1 0.5s ease forwards;
  animation-delay: 0.5s;
}
.startup .content .spanWrap .span2 {
  animation: span2 0.5s ease forwards;
  animation-delay: 1s;
}
.startup .content .spanWrap .span3 {
  animation: span3 0.5s ease forwards;
  animation-delay: 1.5s;
}
.startup .content .textBoxWrap {
  position: absolute;
  top: 0;
  left: 0;
  transform: translateX(-50%);
  animation: textBoxWrap 0.5s forwards;
  animation-delay: 3.5s;
}
.startup .content .textBoxWrap .textBoxWrapInner {
  position: relative;
  width: 200px;
  height: 50px;
  opacity: 0;
  animation: textBoxWrapInner 0.2s forwards;
  animation-delay: 2.5s;
}
.startup .content .textBoxWrap .textBoxWrapInner .textBoxWrapInner__after {
  width: 0;
  height: 100%;
  background-color: orange;
  position: absolute;
  top: 0;
  left: 0;
  animation: textBoxWrapInner__after 1s ease;
  animation-delay: 2.5s;
}
.startup .content .textBoxWrap .textBoxWrapInner .authorText {
  text-transform: capitalize;
  font-size: 21px;
  font-weight: 600;
  color: black;
  font-style: italic;
  text-align: center;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  transform: translateY(50%);
  animation: authorText 0.5s forwards;
  animation-delay: 3s;
}

@keyframes span1 {
  0% {
    transform: translateY(50px);
    opacity: 0;
  }
  100% {
    transform: translateY(0);
    opacity: 100;
  }
}
@keyframes span2 {
  0% {
    transform: translateY(50px);
    opacity: 0;
  }
  100% {
    transform: translateY(0);
    opacity: 100;
  }
}
@keyframes span3 {
  0% {
    transform: translateY(50px);
    opacity: 0;
  }
  100% {
    transform: translateY(0);
    opacity: 100;
  }
}
@keyframes spanWrap {
  0% {
    transform: translateX(-50%);
    opacity: 100;
  }
  100% {
    transform: translateX(-50%) translateY(-50px);
    opacity: 0;
  }
}
@keyframes preloaderYellowBox {
  0% {
    width: 0px;
  }
  50% {
    width: 200px;
    transform: translateX(20px);
  }
  100% {
    width: 0px;
    transform: translateX(-20px);
  }
}
@keyframes textBoxWrapInner {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 100;
  }
}
@keyframes textBoxWrapInner__after {
  0% {
    width: 0;
  }
  50% {
    width: 100%;
  }
  100% {
    width: 0;
  }
}
@keyframes authorText {
  0% {
    color: black;
  }
  100% {
    color: white;
  }
}
@keyframes textBoxWrap {
  0% {
    transform: translateX(-50%) translateY(0);
    opacity: 100;
  }
  100% {
    transform: translateX(-50%) translateY(-50px);
    opacity: 0;
  }
}
@keyframes startup {
  0% {
    opacity: 100;
  }
  100% {
    opacity: 0;
    visibility: hidden;
  }
}
@media screen and (max-width: 600px) {
  .startup .content .spanWrap {
    flex-direction: column;
  }
  .startup .content .spanWrap .span {
    font-size: 21px;
    font-weight: 400;
    padding: 7px;
  }
  .startup .content .textBoxWrap .textBoxWrapInner {
    width: 176px;
    height: 45px;
  }
  .startup .content .textBoxWrap .textBoxWrapInner .authorText {
    font-size: 18px;
  }
}
.BackToTop__Btn__Script {
  visibility: visible !important;
  opacity: 100 !important;
  transform: translateY(0) !important;
}

.BackToTop__Btn {
  visibility: hidden;
  opacity: 0;
  transform: translateY(30px);
  transition: 0.4s ease-out;
  position: fixed;
  z-index: 500;
  right: 50px;
  bottom: 50px;
  text-align: center;
}
.BackToTop__Btn a {
  transition: 0.3s ease;
}
.BackToTop__Btn a span {
  font-size: 23px;
  background-color: rgba(255, 255, 255, 0.788);
  padding: 7px;
  border-radius: 100%;
  color: black;
  box-shadow: 2px 2px 5px 1px rgba(0, 0, 0, 0.299);
}
.BackToTop__Btn a:hover {
  transform: scale(1.2);
}

.FloatWindowContainer__Script {
  visibility: visible !important;
  opacity: 100 !important;
  pointer-events: all !important;
}

.FloatWindowContainer {
  visibility: hidden;
  opacity: 0;
  transition: ease 0.4s;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  overflow-y: scroll;
  overflow-x: hidden;
  background-color: #242424;
  z-index: 100;
  pointer-events: none;
}
.FloatWindowContainer .FloatWindow__outter {
  position: relative;
  width: 100%;
  height: 100%;
}
.FloatWindowContainer .FloatWindow__outter .closeFloatWindow__Script {
  transform: translateY(0) !important;
}
.FloatWindowContainer .FloatWindow__outter .closeFloatWindow {
  transform: translateY(-100%);
  background-color: #242424;
  width: 100%;
  color: white;
  display: flex;
  justify-content: space-between;
  padding: 0 15px;
  transition: 0.3s ease;
  overflow: hidden;
}
.FloatWindowContainer .FloatWindow__outter .closeFloatWindow .text p {
  text-transform: capitalize;
  font-size: 14px;
  font-weight: 300;
  padding: 10px 0;
}
.FloatWindowContainer .FloatWindow__outter .closeFloatWindow .icon {
  position: relative;
}
.FloatWindowContainer .FloatWindow__outter .closeFloatWindow .icon .icon_inner {
  cursor: pointer;
  position: absolute;
  top: 0;
  right: 0;
  background-color: #C32865;
  width: -moz-max-content;
  width: max-content;
  height: calc(100% + 50px);
  display: flex;
  align-items: center;
  transform: rotate(-30deg) translateY(-30px);
  padding: 0 4px;
}
.FloatWindowContainer .FloatWindow__outter .closeFloatWindow .icon .icon_inner span {
  color: white;
  font-size: 22px;
  font-weight: 500;
  transform: rotate(30deg);
  transition: 0.3s ease-in;
  pointer-events: none;
}
.FloatWindowContainer .FloatWindow__outter .closeFloatWindow .icon .icon_inner:hover span {
  transform: rotate(120deg);
}
.FloatWindowContainer .FloatWindow__outter .FloatWindow {
  width: 100%;
  height: 100%;
}

