.jarallax {
  position: relative;
  z-index: 0;
}

.jarallax-hero > .jarallax-img,
picture.jarallax-img img {
  position: absolute;
  -o-object-fit: cover;
     object-fit: cover;
  top: 0;
  left: 0;
  z-index: -1;
}

.jarallax-hero {
  min-height: 100vh;
  height: -moz-max-content;
  height: max-content;
}

.header__innerContainer {
  color: #FFFFFF;
  height: 150px;
  transition: 0.3s ease;
}

.header__nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 15px 50px;
  transition: 0.4s ease;
}
.header__nav .logo a {
  display: flex;
  align-items: center;
}
.header__nav .logo a img {
  width: 90px;
}
.header__nav .navigation {
  flex: 1;
  display: flex;
  justify-content: space-between;
}
.header__nav .navigation .contactAncors {
  padding-left: 150px;
  transition: 0.3s ease-in;
  display: flex;
  align-items: center;
}
.header__nav .navigation .contactAncors a {
  font-size: 16px;
  letter-spacing: 0.2px;
  font-weight: 300;
  transition: ease 0.3s;
  position: relative;
}
.header__nav .navigation .contactAncors a::after {
  content: "";
  position: absolute;
  bottom: -5px;
  left: 50%;
  transform: translateX(-50%);
  transition: 0.4s ease;
  background-color: white;
  background-color: #FFFFFF;
  width: 0;
  height: 0.5px;
