}
.header__nav .navigation .navigationAnimation__container .navigationAnimation__toggler .togglerLinesWrap .togglerMenuLine2 {
  top: 50%;
  transform: translateY(-50%);
}
.header__nav .navigation .navigationAnimation__container .navigationAnimation__toggler .togglerLinesWrap .togglerMenuLine3 {
  width: calc(100% - 12px);
  top: 100%;
  transform: translateY(-100%);
  transition: 0.3s ease-in-out;
}
.header__nav .navigation .navigationAnimation__container .navigationAnimation__toggler .togglePlay {
  display: none;
}
.header__nav .navigation .navigationAnimation__toggler__SCRIPT .togglerLinesWrap {
  transform: translateY(50%);
}
.header__nav .navigation .navigationAnimation__toggler__SCRIPT .togglerLinesWrap .togglerMenuLine1 {
  transform: rotate(-45deg) !important;
}
.header__nav .navigation .navigationAnimation__toggler__SCRIPT .togglerLinesWrap .togglerMenuLine2 {
  transform: translateY(-9px) rotate(45deg) !important;
}
.header__nav .navigation .navigationAnimation__toggler__SCRIPT .togglerLinesWrap .togglerMenuLine3 {
  transform: translateY(-12px) rotate(45deg) !important;
  opacity: 0;
}

.header__nav__Script .navigation .contactAncors {
  transform: translateY(-30px);
  opacity: 0;
}
.header__nav__Script .navigation .Links_Ul {
